import Vue from 'vue'
import Router from 'vue-router'
// import LeftLink from '@/components/leftLink'
import APP from '@/App'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'App',
      component: APP
    }
  ]
})



